<template>
  <button
    class="tu-button"
    :disabled="buttonDisabled"
    :class="[
      type ? `tu-button--${type}` : '',
      size ? `tu-button--${size}` : '',
      {
        'is-disabled': buttonDisabled,
        'is-round': round,
        'is-circle': circle,
        'is-loading': loading,
      },
    ]"
    @click="handleClick"
  >
    <i class="tu-icon__loading tu-icon-sync" v-if="loading"></i>
    <i :class="`${icon}`" v-if="icon"></i>
    <template v-if="$slots.default"><slot></slot></template>
  </button>
</template>

<script>
/**
 * button 
 * @description Button 
 * @property {String} type: default | primary | success | info | warning | danger 
 * @property {String} size: small | medium | large 
 * @property {Boolean} round: 
 * @property {Boolean} disabled: 
 * @property {Boolean} circle: 
 */
export default {
  name: "TuButton",

  props: {
    type: {
      type: String,
      default: "default",
    },
    size: String,
    icon: {
      type: String,
      default: "",
    },
    loading: Boolean,
    round: Boolean,
    circle: Boolean,
    disabled: Boolean,
  },

  computed: {
    buttonDisabled() {
      return this.disabled;
    },
  },

  methods: {
    handleClick(evt) {
      this.$emit("click", evt);
    },
  },
};
</script>

